import { inject, observer } from 'mobx-react'
import {
  compose,
  withHandlers,

} from 'recompose'
import cx from 'classnames'
import { TFoot as TFootComponent } from 'features/Table'
import isEmpty from 'lodash/isEmpty'
import reduce from 'lodash/reduce'
import filter from 'lodash/filter'
import forEach from 'lodash/forEach'

type TProps = {
  activeId: string,
  getActualMatchesList: (
    page: string,
    pageId: string,
    tableStats: Array<{}>,
    opps?: boolean,
  ) => Array<any>,
  //@ts-ignore
  getClassName: (param: {}, activeId: string) => void,
  getExtLink: (
    param?: {},
    //@ts-ignore
    tableStats: Array<{}>,
    id: number,
    forOpp: boolean,
  ) => void,
  //@ts-ignore
  param?: { param1_id: {}, param2_id: {} },
  showOpponentStats: boolean,
  page?: string,
  pageId: string,
  selectedMatches: {},
  setType: (arg: string) => void,
  table: {
    selectedParams: Array<{
      //@ts-ignore
      param_id: number | null,
      gear_group_id: number,
      //@ts-ignore
      param1_id?: {},
      //@ts-ignore
      param2_id?: {},
    }>,
  },
  tableStats: Array<{}>,
}

export const tfootComposed = compose<any, TProps>(
  inject(
    'shootsRange',
    'lexics',
    'videoLink',
    'tournamentMatchesTableStore',
  ),
  //@ts-ignore
  withHandlers({
    /**
    *        
    * @param {number} param_id - id 
    * @param {string} lexical_formula -  
    * @param {array} tableStats -   
    * @param {number} id - id   
    * @param {boolean} forOpp -   .        
    */
    getExtLink: ({
      lexics: { lang, page },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => (
      {
        //@ts-ignore
        lexical_formula,
        //@ts-ignore
        param_id,
      },
      //@ts-ignore
      id,
      //@ts-ignore
      forOpp,
    ) => {
      //@ts-ignore
      const checkId = (teamId) => {
        if (page === 'player') return true

        if (forOpp) return teamId !== id

        return teamId === id
      }

      //    
      const teamTableStats = filter(
        tableStats,
        ({ f_param, f_team }) => (
          f_param === param_id && checkId(f_team)),
      )

      if (isEmpty(teamTableStats)) return null

      //         
      //        [number]
      const filteredEvents = reduce(
        teamTableStats,
        (acc, { f_match_event_ids_json: events }) => {
          if (!events) return acc
          //@ts-ignore
          forEach(events, event => acc.push(event))
          return acc
        }, [],
      )

      return request({
        events: filteredEvents,
        lang,
        lexical_formula,
        range: shootsRange,
        type: 'tablesFooter',
      })
    },
    /**
    *        
    * @param {string} type -   
    */
    setType: ({
      tournamentMatchesTableStore: { changeValueType },
    }) => (type: string, position: number) => changeValueType(type, position),
    /**
    *          .
         
    * @param {string} page -   
    * @param {string} pageId - id /
    * @param {array} tableStats -  
    */
    getActualMatchesList: ({ tableStats }) => () => tableStats,
    /**
    *  
    * @param {number} param_id - id 
    * @param {number} param1_id - id      (  %)
    * @param {number} gear_group_id -   
    * @param {number} activeId -  id  (  )
    */
    getClassName: () => (
      {
        gear_group_id,
        param_id,
        param1_id,
      }:
      {
        param_id: number,
        //@ts-ignore
        param1_id?: {},
        gear_group_id: number,
      },
      activeId?: string,
    ) => cx(
      {
        'clickable-cell': !param1_id,
        percents: param1_id,
        'sorted-by': `${param_id}_${gear_group_id}` === activeId,
      },
    ),
  }),
  observer,
)

export const TFoot = tfootComposed(TFootComponent)
