import { compose, withHandlers } from 'recompose'
import cx from 'classnames'

type TParam = {
  gear_group_id: number,
  param1_id: number | null,
  param_id: number,
}

export const clickableClassNameHOC = compose(
  withHandlers({
    /**
     *  
     * @param {number} param_id - id 
     * @param {number} param1_id - id      (  %)
     * @param {number} gear_group_id -   
     * @param {number} activeId -  id  (  )
     */
    getClassName: () => ({
      gear_group_id,
      param_id,
      param1_id,
    }: TParam,
    activeId: string) => cx({
      'clickable-cell': !param1_id,
      percents: param1_id,
      'sorted-by': `${param_id}_${gear_group_id}` === activeId,
    }),

  }),
)
